import React, { useState, useEffect } from 'react'
import hero1 from './Images/desktop-image-hero-1.jpg';
import hero2 from './Images/desktop-image-hero-2.jpg';
import hero3 from './Images/desktop-image-hero-3.jpg';
import './stylesheets/Slider.css';
import arrow from './Images/icon-arrow.svg';
import Nav from './Nav';
import arrowL from './Images/icon-angle-left.svg';
import arrowR from './Images/icon-angle-right.svg';
import imgDark from './Images/image-about-dark.jpg';
import imgLght from './Images/image-about-light.jpg';

function Slider(props) {
    const [curSlide, setCurSlide] = useState(0);
    const [curText, setCurText] = useState(0);

    const slides = [hero1, hero2, hero3];
    const slideText = [
        {
            header: 'Discover innovative ways to decorate',
            text: `We provide unmatched quality, comfort, and style for property owners
                across the country. Our experts combine form and function in bringing your
                vision to life. Create a room in your own style with our collection and make
                your property a reflection of you and what you love.`
        },
        {
            header: 'We are available all across the globe',
            text: `With stores all over the world, it's easy for you to find furniture for
            your home or place of business. Locally, we're in most major cities throughout
            the country. Find the branch nearest you using our store locator. Any questions?
            Don't hesitate to contact us today.`
        },
        {
            header: 'Manufactured with the best materials',
            text: `Our modern furniture stores provide a high level of quality. Our company has
            invested in advanced technology to ensure that every product is made as perfect and as
            consistent as possible. With three decades of experience in this industry, we understand
            what customers want for their home and office.`
        }
    ]
    function handleSlideClick(next) {
        const imgContainer = document.querySelector('.img-container');
        const curHead = document.querySelector('#curHead');
        const curP = document.querySelector('#curP');

        if (next) {
            const previousSlide = document.createElement('img'); //create an image element

            curP.classList.add('no-opacity'); //add class to make text fade out
            curHead.classList.add('no-opacity');
            previousSlide.classList.add('prev-slide'); //add the class so it starts off screen
            previousSlide.id = 'prev-slide'; //add id for transition

            slides[curSlide - 1] //if a previous slide exists in the array
                ? previousSlide.src = slides[curSlide - 1] //go back one
                : previousSlide.src = (slides[slides.length - 1]); //otherwise start array from the end
            imgContainer.prepend(previousSlide); //add the new image offscreen


            setTimeout(() => { //set a 1ms timeout so it transitions
                previousSlide.style.zIndex = '2'; //slides the image overtop the old one
                previousSlide.style.left = '0'; //slides the image in

            }, 1);
            setTimeout(() => { //set timer for length of transition
                slideText[curText - 1]
                    ? setCurText(curText - 1) //change the state so text is updated
                    : setCurText(slideText.length - 1);
                curHead.classList.remove('no-opacity'); //remove class to fade back in
                curP.classList.remove('no-opacity');

                slides[curSlide - 1] //set the state based on if prev slide exists
                    ? setCurSlide(curSlide - 1) //subtract one from current slide-state
                    : setCurSlide(slides.length - 1); //otherwise start it at end of array
                previousSlide.remove(); //remove the slide that slid in
            }, 400);
        } else { //else do the reverse :) :)
            const nextSlide = document.createElement('img');
            curP.classList.add('no-opacity');
            curHead.classList.add('no-opacity');

            nextSlide.classList.add('next-slide');
            nextSlide.id = 'next-slide';
            slides[curSlide + 1]
                ? nextSlide.src = slides[curSlide + 1]
                : nextSlide.src = (slides[0]);
            imgContainer.prepend(nextSlide);
            setTimeout(() => {
                nextSlide.style.zIndex = '2';
                nextSlide.style.left = '0';

            }, 1);
            setTimeout(() => {
                slideText[curText + 1]
                    ? setCurText(curText + 1)
                    : setCurText(0);
                curHead.classList.remove('no-opacity');
                curP.classList.remove('no-opacity');

                slides[curSlide + 1]
                    ? setCurSlide(curSlide + 1)
                    : setCurSlide(0);
                nextSlide.remove();
            }, 400);
        }
    }
    useEffect(() => {
        const id = setTimeout(() => handleSlideClick(true), 10000);
        return () => clearTimeout(id);
    })

    function darken() {
        const all = [].slice.call(document.querySelectorAll('.dark-me'));

        for (let a of all) {
            a.classList.toggle('darken');
        }

    }
    return (
        <div className="full-page">
            <Nav darken={darken} stopScroll={props.stopScroll} />
            <div className="top flex-row dark-me">
                <div className="img-container">
                    <img src={slides[curSlide]} alt="" className="slide" id="cur-slide" />
                    <div className="arrow-container" id="phone-screen">
                        <div onClick={() => handleSlideClick(false)} className="arrow"><img src={arrowL} alt="" /></div>
                        <div onClick={() => handleSlideClick(true)} className="arrow"><img src={arrowR} alt="" /></div>
                    </div>
                </div>

                <div className="hero-text">

                    <h1 id="curHead">{slideText[curText].header}</h1>
                    <p id="curP">{slideText[curText].text}</p>

                    <a href="#"><h3>SHOP NOW</h3><img src={arrow} alt="" /></a>

                    <div className="arrow-container" id="max-screen">
                        <div onClick={() => handleSlideClick(false)} className="arrow"><img src={arrowL} alt="" /></div>
                        <div onClick={() => handleSlideClick(true)} className="arrow"><img src={arrowR} alt="" /></div>
                    </div>
                </div>
            </div>
            <div className="bottom flex-row">

                <img src={imgDark} alt="" />

                <div className="about-text">
                    <h3>ABOUT OUR FURNITURE</h3>
                    <p>Our multifunctional collection blends design and function to suit
                    your individual taste. Make each room unique, or pick a cohesive
                    theme that best express your interests and what inspries you.
                    Find the furniture pieces you need, from traditional to contemporary
                    styles or anything in between. Product specialists are available to
                    help you create your dream space.
                    </p>
                </div>
                <img src={imgLght} alt="" />
            </div>
        </div>
    )
}

export default Slider;